.infographic {
	width: 100%;
	height: 407px;
	padding: 35px 0 20px;
	float: left;
	position: relative;
	margin-bottom: 50px;
	box-sizing: content-box !important;
	
	overflow: hidden;
	/** GENERAL CLASSES FOR EXTEND PURPOSE **/
	.active {
		display: block !important;
	}
	/** END GENERAL CLASSES **/
	* {
		box-sizing: content-box !important;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	}
	.graphics {
		width: 100%;
		height: 407px;
		position: absolute;
		left: 0;
		&.selected-type-1 {
			left: -238px;
		}
		&.selected-type-2,
		&.selected-type-3 {
			left: -208px;
		}
		ul {
			display: none;
			float: left;
		}
		li {
			padding: 0 14px 0 11px;
			border-right: solid 2px #fff;
			color: #fff;
			float: left;
			font: normal 12px Arial;
			&.last {
				border-right: none;
			}
		}
	}
	.graphics-wrapper {
		position: relative;
		width: 667px;
		height: 405px;
		margin: 0 auto;
		overflow: visible;
	}
	.border {
		@extend .infographic-infographic-border;
		position: absolute;
		top: -1px;
		left: -1px;
		&.inactive {
			@extend .infographic-inactive-infographic-border;
		}
	}
	.client-value {
		display: inline-block;
		width: auto;
		height: 35px;
		background: #f4f6f7;
		font: normal 13px Arial;
		text-align: center;
		color: $text-primary;
		position: relative;
		top: -34px;
		padding: 0 10px;
		em {
			display: block;
			font: normal 15px Arial;
		}
		&.inactive {
			color: #cfd1d4 !important;
		}
	}
	.title {
		display: inline-block;
		float: left;
		em {
			display: inline-block;
			font: normal 48px $main-font;
			color: #fff;
			float: left;
		}
		h2 {
			display: inline-block;
			font: normal 13px Arial;
			color: #fff;
			vertical-align: top;
			margin: 8px 0 0 5px;
		}
	}
	.details {
		position: absolute;
		bottom: 15px;
		right: 15px;
	}
	.graphic-type-1 {
		display: none;
		width: 653px;
		height: 91px;
		background: #0c1645;
		cursor: pointer;
		z-index: 1;
		-webkit-user-select: none;
		position: absolute;
		bottom: 94px;
		left: 7px;
		box-sizing: content-box !important;

		li {
			height: 40px;
		}
		.title {
			margin: 17px 0 0 20px;
			h2 {
				margin-top: 8px;
				width: 100px;
			}
		}
		.details {
			@extend .infographic-info-type-1;
		}
		&.hover {
			z-index: 5;
			width: 744px;
			height: 99px;
			bottom: 89px;
			left: -39px;
			ul {
				@extend .active;
				margin-left: 7px;
				margin-top: 27px;
				padding: 0;
			}
			.details {
				@extend .infographic-details-type-1;
				&:hover {
					@extend .infographic-hover-details-type-1;
				}
			}
		}
		&.active {
			z-index: 5;
			width: 744px;
			height: 99px;
			bottom: 89px;
			left: -39px;
			cursor: default;
			ul {
				@extend .active;
				margin-left: 7px;
				margin-top: 27px;
			}
			.details {
				display: none;
			}
		}
		&.inactive,
		&.inactive:hover {
			@extend .infographic-inactive-graphic-type-1;
			width: 653px;
			height: 91px;
			bottom: 94px;
			left: 7px;
			z-index: 1;
			cursor: default;
			.title {
				text-indent: 0px;
				em, h2 {
					color: #c2c5d0;
				}
			}
			ul {
				display: none !important;
			}
			.details {
				@extend .infographic-sprite;
				@extend .infographic-inactive-info-type-1;
			}
		}
	}
	.graphic-type-2 {
		display: none;
		width: 392px;
		height: 91px;
		background: #e29317;
		cursor: pointer;
		z-index: 2;
		-webkit-user-select: none;
		position: absolute;
		bottom: 192px;
		left: 138px;
		box-sizing: content-box !important;

		li {
			height: 47px;
		}
		.title {
			margin: 18px 0 0 24px;
			h2 {
				margin-right: 7px;
				width: 70px;
			}
		}
		.details {
			@extend .infographic-info-type-2;
		}
		&.hover {
			z-index: 5;
			width: 652px;
			height: 99px;
			bottom: 187px;
			left: 7px;
			ul {
				@extend .active;
				margin-left: 17px;
				margin-top: 30px;
				padding: 0;
			}
			.details {
				@extend .infographic-details-type-2;
				&:hover {
					@extend .infographic-hover-details-type-2;
				}
			}
		}
		&.active {
			z-index: 5;
			width: 652px;
			height: 99px;
			bottom: 187px;
			left: 7px;
			cursor: default;
			ul {
				@extend .active;
				margin-left: 17px;
				margin-top: 30px;
			}
			.details {
				display: none;
			}
		}
		&.inactive,
		&.inactive:hover {
			@extend .infographic-inactive-graphic-type-2;
			width: 392px;
			height: 91px;
			bottom: 192px;
			left: 138px;
			z-index: auto;
			cursor: default;
			z-index: 2;
			.title {
				text-indent: 0px;
				em, h2 {
					color: #f3e3c9;
				}
			}
			ul {
				display: none !important;
			}
			.details {
				@extend .infographic-sprite;
				@extend .infographic-inactive-info-type-2;
			}
		}
	}
	.graphic-type-3 {
		@extend .infographic-graphic-type-3;
		display: none;
		width: 131px;
		height: 297px;
		padding-top: 26px;
		cursor: pointer;
		z-index: 3;
		-webkit-user-select: none;
		position: absolute;
		bottom: 77px;
		left: 268px;
		box-sizing: content-box !important;

		.title {
			text-indent: 0px;
			margin: 12px 0 0 9px;
			h2 {
				width: 85px;
			}
		}
		.details {
			@extend .infographic-info-type-3;
		}
		&.hover {
			ul {
				@extend .active;
			}
			.details {
				@extend .infographic-details-type-3;
				&:hover {
					@extend .infographic-hover-details-type-3;
				}
			}
		}
		&.active {
			z-index: 5;
			cursor: default;
			ul {
				@extend .active;
			}
			.details {
				display: none;
			}
		}
		&.inactive,
		&.inactive:hover {
			@extend .infographic-inactive-graphic-type-3;
			width: 131px;
			height: 297px;
			padding-top: 26px;
			cursor: default;
			.point {
				display: none;
			}
			.title {
				em, h2 {
					color: #dcdee0;
				}
			}
			ul {
				display: none !important;
			}
			.details {
				@extend .infographic-sprite;
				@extend .infographic-inactive-info-type-3;
			}
		}
	}
	.single-gateway {
		position: absolute;
		top: 335px;
		left: 7px;
		font: normal 13px Arial;
		text-align: center;
		color: $text-primary;
		&.inactive {
			color: #cfd1d4;
			i {
				@extend .infographic-inactive-gateway-bar;
			}
		}
	}
	.client {
		@extend .infographic-client-arrows;
		padding: 0;
		text-indent: 0px;
		position: absolute;
		bottom: -13px;
		left: 302px;
		&.inactive {
			@extend .infographic-inactive-client-arrows;
		}
	}

	.client-label {
		width: 100%;
		position: absolute;
		bottom: -23px;
		left: 0;
		font: normal 13px Arial;
		text-align: center;
		color: $text-primary;

		&.inactive {
			color: #cfd1d4;
		}
	}
	.aside {
		height: 407px;
		width: 260px;
		padding: 0 32px 0 20px;
		border-left: solid 1px #cfd1d4;
		display: none;
		overflow: hidden;
		position: absolute;
		right: 0px;
		h2 {
			position: relative;
			margin: 0 0 7px;
			font: normal 24px / 24px $main-font;
			// vertical-align: top;

		}
		p {
			font: normal 13px/18px Arial;
			color: $text-primary;
			margin: 0 0 18px;
		}
		li {
			margin-bottom: 36px;
		}
		.close-button {
			position: absolute;
			top: -20px;
			right: -20px;
			z-index: 10;
			padding: 20px;
			cursor: pointer;
		}
		.close {
			@extend .infographic-close-inline;
		}
		.content-type-1,
		.content-type-2,
		.content-type-3 {
			display: none;
		}
		// Setting jScrollPane styles for infographic
		.jspVerticalBar {
			width: 4px;
			height: 373px;
			position: absolute;
			top: 33px;
			right: 6px;
		}
		.jspCap {
			display: none !important;
		}
		.jspHorizontalBar {
			display: none;
		}
		.jspTrack {
			display: block;
			background: #d2d3d4;
			height: 100%;
		}
		.jspDrag {
			display: block;
			background: #e29317;
		}
	}
}


.infographic {
	@include respond-to('xl') {
		.graphics {
			&.selected-type-1,
			&.selected-type-2,
			&.selected-type-3 {
				left: -50%;
			}

		}
	}
	@include respond-to("large") {
		height: auto !important;
		padding: 0;
		.graphics {
			display: none;
		}
		.aside {
			display: block;
			overflow: visible !important;
			width: auto !important;
			height: auto !important;
			position: static !important;
			border: none;
			padding: 0;
			.jspPane {
				position: static;
				padding: 0 !important;
				margin: 0 !important;
			}
			.content-type-1,
			.content-type-2,
			.content-type-3 {
				display: block;
				height: auto !important;
			}
			.close-button {
				display: none;
			}
			.jspVerticalBar {
				display: none;
			}
		}
		.jspContainer {
			display: block;
			overflow: visible !important;
			width: auto !important;
			height: auto !important;
			position: static !important;
		}
	}
}

@include respond-to('large') {
	.infographic-fallback {
		display: block;
		margin-bottom: 20px;

		img {
			width: 100%;
			height: auto;
		}
	}

	.infographic .aside {
		.content-type-1, .content-type-2, .content-type-3 {
			h2 {
				margin-top: 5px;
			}
		}
	}
}
