@mixin respond-to($media) {
	@if $media == small {
		@media (max-width: $break-small) {
			@content;
		}
	}
	@else if $media == medium-and-up {
		@media (min-width: $break-medium) {
			@content;
		}
	}
	@else if $media == medium {
		@media (max-width: $break-medium) {
			@content;
		}
	}
	@else if $media == large-intermediate {
		@media (max-width: $break-large-intermediate) {
			@content;
		}
	}
	@else if $media == large {
		@media (max-width: $break-large) {
			@content;
		}
	}
	@else if $media == xlarge {
		@media (max-width: $break-xlarge) {
			@content;
		}
	}
	@else if $media == up-than-large {
		@media (min-width: $break-large + 1) {
			@content;
		}
	}
	@else if $media == up-than-xlarge {
		@media (min-width: $break-xlarge) {
			@content;
		}
	}
	@else if $media == wide {
		@media (min-width: $break-wide) {
			@content;
		}
	}
}

//
// Internet explorer 8, 9, 10
//
@mixin ie8 {
	.lt-ie10.lt-ie9 {
		@content;
	}
}

@mixin ie9 {
	.lt-ie10 {
		@content;
	}
}

@mixin ie10 {
	.lt-ie11 {
		@content;
	}
}


@mixin disable-select {
	@include vendor-prefix(user-select, none);
}

@mixin get-map-positioning($x, $y, $w, $h) {
	$top: ($y * 100 / $h) * 1%;
	$left: ($x * 100 / $w) * 1%;

	top: $top;
	left: $left;
	top: $top - 2%\9;
	left: $left - 2%\9;
}


//
// Opacity with fallback
//
@mixin opacity($opacity) {
	$ie: $opacity * 100;
	-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=" + $ie + ")";
  	filter: alpha(opacity=$ie);
	opacity: $opacity;
}


//
// Generic vendor prefix
//
@mixin vendor-prefix($name, $argument) {
	#{$name}: $argument;
	-webkit-#{$name}: $argument;
	-moz-#{$name}: $argument;
	-ms-#{$name}: $argument;
	-o-#{$name}: $argument;
}


//
// Vendor-prefixed transitions
// reference: http://log.vieron.net/articles/sass-mixins-for-vendor-prefixed-transitions-including-properties/
//
@mixin vendor-prefix-transition($values...) {
	@function prefix($property, $prefixes: (webkit moz o ms)) {
		$vendor-prefixed-properties: transform background-clip background-size;
		$result: ();
		@each $prefix in $prefixes {
			@if index($vendor-prefixed-properties, $property) {
				$property: -#{$prefix}-#{$property}
			}
			$result: append($result, $property);
		}
		@return $result;
	}

	@function trans-prefix($transition, $prefix: moz) {
		$prefixed: ();
		@each $trans in $transition {
			$prop-name: nth($trans, 1);
			$vendor-prop-name: prefix($prop-name, $prefix);
			$prop-vals: nth($trans, 2);
			$prefixed: append($prefixed, ($vendor-prop-name $prop-vals), comma);
		}

		@return $prefixed;
	}

	$transitions: ();
	@each $declaration in $values {
		$prop: nth($declaration, 1);
		$prop-opts: ();
		$length: length($declaration);
		@for $i from 2 through $length {
			$prop-opts: append($prop-opts, nth($declaration, $i));
        }
		$trans: ($prop, $prop-opts);
		$transitions: append($transitions, $trans, comma);
    }

	-webkit-transition: trans-prefix($transitions, webkit);
	-moz-transition: trans-prefix($transitions, moz);
	-o-transition: trans-prefix($transitions, o);
	transition: $values;
}
