$height: 490px;
$contentWidth: 312px;
$wrapperWidth: 980px + $contentWidth;
$padding: 20px;

.box-gateway {
	* {
		box-sizing: content-box !important;
	}
	width: 980px - $padding * 2;
	height: $height;
	position: relative;
	margin: 0 auto;
	overflow: hidden;
	@include vendor-prefix(user-select, none);
	.box-gateway-wrapper {
		width: $wrapperWidth;
		height: $height;
		position: absolute;
		left: 0;
		top: 0;
	}
	$infographicLine: 980px - $padding * 2;
	.box-gateway-infographic {
		float: left;
		width: $infographicLine;
		margin-bottom: 20px;
		height: $height;
		$width: 653px;
		#infographic-area {
			width: $infographicLine;
			height: $height;
			text-align: center;
			& > div {
				display: inline-block !important;
				width: $width;
				height: $height;
			}
		}
		#infographic-area-mask {
			@extend #infographic-area;
			position: absolute;
			top: 0;
		}
	}
	$scrollHeight: $height - 40px;
	.box-gateway-content {
		position: relative;
		float: left;
		width: 300px;
		height: $scrollHeight;
		padding: 0 32px 0 20px;
		border-left: solid 1px #cfd1d4;
		margin-top: 5px;
		@include opacity(0);
		h2 {
			font-size: 24px;
			line-height: 28px;
		}
		h3 {
			font-size: 18px;
			line-height: 22px;
		}
		.scroll-content {
			display: none;
		}
		$topSpace: 33px;
		.jspVerticalBar {
			width: 4px;
			height: $scrollHeight - $topSpace;
			position: absolute;
			right: 6px;
			background-color: transparent;
			.jspCapTop {
				display: block;
				height: $topSpace;
			}
		}
		.jspTrack {
			display: block;
			background: #d2d3d4;
			height: 100%;
		}
		.jspDrag {
			display: block;
			background: #e29317;
		}
		.close-button {
			position: absolute;
			top: -20px;
			right: -20px;
			z-index: 10;
			padding: 20px;
			cursor: pointer;
		}
		.close {
			@extend .infographic-close-inline;
		}
	}
}

@media (max-width: 1023px) {
	.box-gateway {
		width: 100%;
		height: auto;
		.box-gateway-wrapper {
			width: 100%;
			height: auto;
			position: static;
		}
		.box-gateway-infographic,
		a.close-button {
			display: none !important;
		}
		.box-gateway-content {
			width: 100% !important;
			height: auto !important;
			filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
			opacity: 1;
			border: none;
			padding: 0;
			.scroll-content {
				display: block;
			}
		}
		.jspContainer {
			display: block;
			overflow: visible !important;
			width: 100% !important;
			height: auto !important;
			position: static !important;
		}
		.jspPane {
			position: static;
			padding: 0 !important;
			margin: 0 !important;
			width: auto !important;
		}

		.infographic-fallback {
			display: block;
			margin-bottom: 20px;

			img {
				width: 100%;
				height: auto;
			}
		}
	}
}
